in-left: 40px;\r\n    }\r\n\r\n    .m-r-5 {\r\n        margin-right: 5px;\r\n    }\r\n\r\n    .m-r-10 {\r\n        margin-right: 10px;\r\n    }\r\n\r\n    .m-r-15 {\r\n        margin-right: 15px;\r\n    }\r\n\r\n    .m-r-20 {\r\n        margin-right: 20px;\r\n    }\r\n\r\n    .m-r-30 {\r\n        margin-right: 30px;\r\n    }\r\n\r\n    .m-r-40 {\r\n        margin-right: 40px;\r\n    }\r\n\r\n    .m-t-0 {\r\n        margin-top: 0px;\r\n    }\r\n\r\n    .m-t-5 {\r\n        margin-top: 5px;\r\n    }\r\n\r\n    .m-t-10 {\r\n        margin-top: 10px;\r\n    }\r\n\r\n    .m-t-15 {\r\n        margin-top: 15px;\r\n    }\r\n\r\n    .m-t-20 {\r\n        margin-top: 20px;\r\n    }\r\n\r\n    .m-t-30 {\r\n        margin-top: 30px;\r\n    }\r\n\r\n    .m-t-40 {\r\n        margin-top: 40px;\r\n    }\r\n\r\n    .m-b-0 {\r\n        margin-bottom: 0px;\r\n    }\r\n\r\n    .m-b-5 {\r\n        margin-bottom: 5px;\r\n    }\r\n\r\n    .m-b-10 {\r\n        margin-bottom: 10px;\r\n    }\r\n\r\n    .m-b-15 {\r\n        margin-bottom: 15px;\r\n    }\r\n\r\n    .m-b-20 {\r\n        margin-bottom: 20px;\r\n    }\r\n\r\n    .m-b-30 {\r\n        margin-bottom: 30px;\r\n    }\r\n\r\n    .m-b-40 {\r\n        margin-bottom: 40px;\r\n    }\r\n}\r\n\r\n/*******************\r\nfont weight\r\n*******************/\r\n\r\nhtml body {\r\n    .font-bold {\r\n        font-weight: 700;\r\n    }\r\n\r\n    .font-normal {\r\n        font-weight: normal;\r\n    }\r\n\r\n    .font-light {\r\n        font-weight: 300;\r\n    }\r\n    .font-medium {\r\n        font-weight: 500;\r\n    }\r\n    .font-16 {\r\n        font-size: 16px;\r\n    }\r\n    .font-14 {\r\n        font-size: 14px;\r\n    }\r\n    .font-10 {\r\n        font-size: 10px;\r\n    }\r\n    .font-18 {\r\n        font-size: 18px;\r\n    }\r\n    .font-20 {\r\n        font-size: 20px;\r\n    }\r\n}\r\n\r\n/*******************\r\nBorder\r\n*******************/\r\n\r\nhtml body {\r\n    .b-0 {\r\n        border: none;\r\n    }\r\n    .b-r {\r\n        border-right: 1px solid $border;\r\n    }\r\n    .b-l {\r\n        border-left: 1px solid $border;\r\n    }\r\n    .b-b {\r\n        border-bottom: 1px solid $border;\r\n    }\r\n    .b-t {\r\n        border-top: 1px solid $border;\r\n    }\r\n    .b-all {\r\n        border: 1px solid $border!important;\r\n    }\r\n}\r\n\r\n\r\n\r\n.hide {\r\n    display: none;\r\n}\r\n\r\n.img-circle {\r\n    border-radius: 100%;\r\n}\r\n\r\n.radius {\r\n    border-radius: $radius;\r\n}\r\n\r\n\r\n\r\n\r\n\r\n/*******************\r\nText Colors\r\n*******************/\r\n\r\n.text-white {\r\n    color: $white!important;\r\n}\r\n\r\n.text-danger {\r\n    color: $danger!important;\r\n}\r\n\r\n.text-muted {\r\n    color: $muted!important;\r\n}\r\n\r\n.text-warning {\r\n    color: $warning!important;\r\n}\r\n\r\n.text-success {\r\n    color: $success!important;\r\n}\r\n\r\n.text-info {\r\n    color: $info!important;\r\n}\r\n\r\n.text-inverse {\r\n    color: $inverse!important;\r\n}\r\n\r\nhtml body {\r\n    .text-blue {\r\n        color: $blue;\r\n    }\r\n\r\n    .text-purple {\r\n        color: $purple;\r\n    }\r\n\r\n    .text-primary {\r\n        color: $primary;\r\n    }\r\n\r\n    .text-megna {\r\n        color: $megna;\r\n    }\r\n\r\n    .text-dark {\r\n        color: $bodytext;\r\n    }\r\n    .text-themecolor {\r\n        color: $themecolor;\r\n    }\r\n}\r\n\r\n\r\n\r\n\r\n\r\n/*******************\r\nBackground Colors\r\n*******************/\r\n\r\n.bg-primary {\r\n    background-color: $primary !important;\r\n}\r\n\r\n.bg-success {\r\n    background-color: $success !important;\r\n}\r\n\r\n.bg-info {\r\n    background-color: $info !important;\r\n}\r\n\r\n.bg-warning {\r\n    background-color: $warning !important;\r\n}\r\n\r\n.bg-danger {\r\n    background-color: $danger !important;\r\n}\r\n\r\nhtml body {\r\n    .bg-megna {\r\n        background-color: $megna;\r\n    }\r\n    .bg-theme {\r\n        background-color: $themecolor;\r\n    }\r\n\r\n    .bg-inverse {\r\n        background-color: $inverse;\r\n    }\r\n\r\n    .bg-purple {\r\n        background-color: $purple;\r\n    }\r\n    .bg-light-part {\r\n        background-color: $bglight;\r\n    }\r\n    .bg-light-primary {\r\n        background-color: $light-primary;\r\n    }\r\n\r\n    .bg-light-success {\r\n        background-color: $light-success;\r\n    }\r\n\r\n    .bg-light-info {\r\n        background-color: $light-info;\r\n    }\r\n    .bg-light-extra {\r\n        background-color: $extra-light;\r\n    }\r\n\r\n    .bg-light-warning {\r\n        background-color: $light-warning;\r\n    }\r\n\r\n    .bg-light-danger {\r\n        background-color: $light-danger;\r\n    }\r\n\r\n    .bg-light-inverse {\r\n        background-color: $light-inverse;\r\n    }\r\n    .bg-light {\r\n        background-color: $light;\r\n    }\r\n    .bg-white {\r\n        background-color: $white;\r\n    }\r\n}\r\n\r\n\r\n\r\n\r\n\r\n/*******************\r\nRounds\r\n*******************/\r\n\r\n.round {\r\n    line-height: 48px;\r\n    color: $white;\r\n    width: 50px;\r\n    height: 50px;\r\n    display: inline-block;\r\n    font-weight: 400;\r\n    text-align: center;\r\n    border-radius: 100%;\r\n    background: $info;\r\n    img {\r\n        border-radius: 100%;\r\n    }\r\n}\r\n\r\n.round-lg {\r\n    line-height: 65px;\r\n    width: 60px;\r\n    height: 60px;\r\n    font-size: 30px;\r\n}\r\n\r\n.round.round-info {\r\n    background: $info;\r\n}\r\n\r\n.round.round-warning {\r\n    background: $warning;\r\n}\r\n\r\n.round.round-danger {\r\n    background: $danger;\r\n}\r\n\r\n.round.round-success {\r\n    background: $success;\r\n}\r\n\r\n.round.round-primary {\r\n    background: $primary;\r\n}\r\n\r\n/*******************\r\nPagination\r\n*******************/\r\n\r\n.pagination>li:first-child>a,\r\n.pagination>li:first-child>span {\r\n    border-bottom-left-radius: $radius;\r\n    border-top-left-radius: $radius;\r\n}\r\n\r\n.pagination>li:last-child>a,\r\n.pagination>li:last-child>span {\r\n    border-bottom-right-radius: $radius;\r\n    border-top-right-radius: $radius;\r\n}\r\n\r\n.pagination>li>a,\r\n.pagination>li>span {\r\n    color: $dark;\r\n}\r\n\r\n.pagination>li>a:hover,\r\n.pagination>li>span:hover,\r\n.pagination>li>a:focus,\r\n.pagination>li>span:focus {\r\n    background-color: $light;\r\n}\r\n\r\n.pagination-split li {\r\n    margin-left: 5px;\r\n    display: inline-block;\r\n    float: $lft;\r\n}\r\n\r\n.pagination-split li:first-child {\r\n    margin-left: 0;\r\n}\r\n\r\n.pagination-split li a {\r\n    -moz-border-radius: $radius;\r\n    -webkit-border-radius: $radius;\r\n    border-radius: $radius;\r\n}\r\n\r\n.pagination>.active>a,\r\n.pagination>.active>span,\r\n.pagination>.active>a:hover,\r\n.pagination>.active>span:hover,\r\n.pagination>.active>a:focus,\r\n.pagination>.active>span:focus {\r\n    background-color: $themecolor;\r\n    border-color: $themecolor;\r\n}\r\n\r\n.pager li>a,\r\n.pager li>span {\r\n    -moz-border-radius: $radius;\r\n    -webkit-border-radius: $radius;\r\n    border-radius: $radius;\r\n    color: $dark;\r\n}\r\n\r\n\r\n\r\n\r\n\r\n\r\n/*******************\r\nTable Cell\r\n*******************/\r\n\r\n.table-box {\r\n    display: table;\r\n    width: 100%;\r\n}\r\n\r\n.table.no-border {\r\n    tbody {\r\n        td {\r\n            border: 0px;\r\n        }\r\n    }\r\n}\r\n\r\n.cell {\r\n    display: table-cell;\r\n    vertical-align: middle;\r\n}\r\n\r\n.table td,\r\n.table th {\r\n    border-color: $table-border;\r\n}\r\n\r\n.table thead th,\r\n.table th {\r\n    font-weight: 500;\r\n}\r\n\r\n.table-hover tbody tr:hover {\r\n    background: $light;\r\n}\r\n\r\n.lite-padding td {\r\n    padding: 5px;\r\n}\r\n\r\n.v-middle {\r\n    td,\r\n    th {\r\n        vertical-align: middle;\r\n    }\r\n}\r\n\r\n.table-responsive {\r\n    display: block;\r\n    width: 100%;\r\n    overflow-x: auto;\r\n    -ms-overflow-style: -ms-autohiding-scrollbar;\r\n}\r\n\r\n/*Badge*/\r\n\r\n.badge {\r\n    font-weight: 400;\r\n}\r\n\r\n.badge-xs {\r\n    font-size: 9px;\r\n}\r\n\r\n.badge-xs,\r\n.badge-sm {\r\n    -webkit-transform: translate(0, -2px);\r\n    -ms-transform: translate(0, -2px);\r\n    -o-transform: translate(0, -2px);\r\n    transform: translate(0, -2px);\r\n}\r\n\r\n.badge-success {\r\n    background-color: $success;\r\n}\r\n\r\n.badge-info {\r\n    background-color: $info;\r\n}\r\n\r\n.badge-primary {\r\n    background-color: $primary;\r\n}\r\n\r\n.badge-warning {\r\n    background-color: $warning;\r\n}\r\n\r\n.badge-danger {\r\n    background-color: $danger;\r\n}\r\n\r\n.badge-purple {\r\n    background-color: $purple;\r\n}\r\n\r\n.badge-red {\r\n    background-color: $red;\r\n}\r\n\r\n.badge-inverse {\r\n    background-color: $inverse;\r\n}\r\n\r\n/*******************\r\nButtons\r\n******************/\r\n\r\n.btn {\r\n    padding: 7px 12px;\r\n\r\n    cursor: pointer;\r\n}\r\n\r\n.btn-group {\r\n    label {\r\n        color: $white!important;\r\n        margin-bottom: 0px;\r\n        &.btn-secondary {\r\n            color: $bodytext!important;\r\n        }\r\n    }\r\n}\r\n\r\n.btn-lg {\r\n    padding: .75rem 1.5rem;\r\n    font-size: 1.25rem;\r\n}\r\n\r\n.btn-md {\r\n    padding: 12px 55px;\r\n    font-size: 16px;\r\n}\r\n\r\n.btn-circle {\r\n    border-radius: 100%;\r\n    width: 40px;\r\n    height: 40px;\r\n    padding: 10px;\r\n}\r\n\r\n.btn-circle.btn-sm {\r\n    width: 35px;\r\n    height: 35px;\r\n    padding: 8px 10px;\r\n    font-size: 14px;\r\n}\r\n\r\n.btn-circle.btn-lg {\r\n    width: 50px;\r\n    height: 50px;\r\n    padding: 14px 15px;\r\n    font-size: 18px;\r\n    line-height: 22px;\r\n}\r\n\r\n.btn-circle.btn-xl {\r\n    width: 70px;\r\n    height: 70px;\r\n    padding: 14px 15px;\r\n    font-size: 24px;\r\n}\r\n\r\n.btn-sm {\r\n    padding: .25rem .5rem;\r\n    font-size: 12px;\r\n}\r\n\r\n.btn-xs {\r\n    padding: .25rem .5rem;\r\n    font-size: 10px;\r\n}\r\n\r\n.button-list button,\r\n.button-list a {\r\n    margin: 5px 12px 5px 0;\r\n}\r\n\r\n.btn-